/* eslint-disable spellcheck/spell-checker */
import { binMatrix } from './brandBinMatrix';
import getRawValue from './getRawValue';
import getPossibleBrand from './possibleBrand.js';
import { AMEX, DISCOVER, ELO, INVALID, MASTERCARD, UNKNOWN, VISA } from '../constants';

/**
 * @description execute for loop for common use cases to determine if passed digits map to a valid bin
 * Note: This method is designed to be called only when the card brand is known && when digits.length is > 1.
 It is not designed to be called directly by the card number component, but, rather, is called only as a
 result of calling `__getIsValidBin` method, also located in this file.
 Putting all the card bin/brand validation logic together it's not possible for this method to be called unless
 digits.length > 1 && there are specific possibleBins.
 *
 * @param {string} digits
 * @param {array} possibleBins
 * @returns {bool}
 */
const __checkTwoOrThreeDigits = (digits, possibleBins) => {
  const possibleBinsLength = possibleBins.length;
  let isValidBin = false;

  for (let bin = 0; bin < possibleBinsLength; bin++) {
    if (digits === possibleBins[bin]) {
      isValidBin = true;
      break;
    }
  }
  return isValidBin;
};

/**
 * @description check if provided card number is valid for mastercard
 * currently used only for 2-series mastercards
 *
 * @param {string} cardNum
 * @returns {bool}
 */
const __isValidMasterCard = cardNum => {
  const cardNumsToValidate = cardNum.length < 7 ? cardNum : cardNum.substr(0, 6);
  const validationNumsLength = cardNumsToValidate.length;
  const validationRange = binMatrix[MASTERCARD]['ranges'][validationNumsLength]; // eslint-disable-line dot-notation
  return (
    validationRange.min <= Number(cardNumsToValidate) &&
    Number(cardNumsToValidate) <= validationRange.max
  );
};

/**
 * @description check if provided card number is valid for discover
 * currently used only for discover cards beginning with 622*
 *
 * @param {string} cardNum
 * @returns {bool}
 */
const __isValidDiscoverCard = cardNum => {
  let digitsToCheck;
  let isValidBin;
  let possibleBins;
  const isFewerThanFourDigits = cardNum.length < 4;
  if (cardNum.length === 1) {
    isValidBin = true;
  } else if (cardNum.substr(0, 2) === '65') {
    isValidBin = true;
  } else if (isFewerThanFourDigits) {
    digitsToCheck = cardNum.substr(0, 3);
    possibleBins = binMatrix[DISCOVER]['twoOrThreeDigits']; // eslint-disable-line dot-notation
    isValidBin = __checkTwoOrThreeDigits(digitsToCheck, possibleBins);
  } else if (cardNum.substr(0, 4) === '6011') {
    isValidBin = true;
  } else {
    const cardNumsToValidate = cardNum.substr(0, 8);
    const validationNumsLength = cardNumsToValidate.length;
    const validationRange = binMatrix[DISCOVER]['ranges'][validationNumsLength]; // eslint-disable-line dot-notation
    isValidBin =
      validationRange.min <= Number(cardNumsToValidate) &&
      Number(cardNumsToValidate) <= validationRange.max;
  }
  return isValidBin;
};

/**
 * @description checks bin range of entered card number
 *
 * There is a separate onBlur check for if the entered card number is a complete card number for that brand.
 *
 * The check for whether a card might be elo will return 'elo', 'unknown', or a cardBrand.
 * All cards starting with '4' are valid whether 'elo' or 'visa'.
 * All 'unknown' cards are valid.
 *
 * 'amex' cards currently have only two bin ranges so the for loop is good enough for this brand
 * 'mastercard' 5-series cards have only five bin ranges so the for loop is good enough for this brand
 *
 *
 * @param {string} possibleBrand
 * @param {string} cardNum
 * @returns {bool}
 */
const __getIsValidBin = (possibleBrand, cardNum) => {
  const cardNumLengthOne = cardNum.length === 1;
  let isValidBin = false;
  let possibleBins;
  let firstDigit;
  let digitsToCheck;
  switch (possibleBrand) {
    case ELO:
    case UNKNOWN:
    case VISA:
      isValidBin = true;
      break;
    case AMEX:
      const cardNumLength = cardNum.length;
      digitsToCheck = cardNum.substr(0, 2);
      possibleBins = binMatrix[possibleBrand];
      isValidBin =
        cardNumLength === 1 ? true : __checkTwoOrThreeDigits(digitsToCheck, possibleBins);
      break;
    case MASTERCARD:
      firstDigit = cardNum[0];
      digitsToCheck = cardNum.substr(0, 2);
      const firstDigitIsFive = firstDigit === '5';
      if (cardNumLengthOne) {
        isValidBin = true;
      } else if (firstDigitIsFive) {
        possibleBins = binMatrix[possibleBrand]['twoDigits']; // eslint-disable-line dot-notation
        isValidBin = __checkTwoOrThreeDigits(digitsToCheck, possibleBins);
      } else {
        isValidBin = __isValidMasterCard(cardNum);
      }
      break;
    case DISCOVER:
      isValidBin = __isValidDiscoverCard(cardNum);
      break;
    default:
  }

  return isValidBin;
};

/**
 * @description determines if the entered card number is for a valid cardBrand in a valid bin range
 *
 * @param {string} cardNum
 * @param {array} eloBins
 * @returns {bool}
 */
export default (cardNum, eloBins) => {
  cardNum = getRawValue(cardNum);
  const possibleBrand = getPossibleBrand(cardNum, eloBins);
  const isInvalid = possibleBrand === INVALID || !__getIsValidBin(possibleBrand, cardNum);

  return !isInvalid;
};
