import { AMEX, DISCOVER, ELO, MASTERCARD, VISA } from '../constants';

export const brands = [AMEX, DISCOVER, ELO, MASTERCARD, VISA];

/**
  * If the value's first digit does not map to one of these it will cause the
  `possibleBrand` to be `INVALID` and the validator to return `invalid_bin`.
  */
export const brandMatrix = {
  '2': MASTERCARD,
  '3': AMEX,
  '4': VISA,
  '5': MASTERCARD,
  '6': DISCOVER
};

/**
  * If the value's first digit maps to one of the `brandMatrix` brands but the rest of the
  value, aka 'bin,' does not follow the below logic, it will cause the `possibleBrand` to be
  one of the known brands in `brandMatrix` and the validator to return `invalid_bin_brand`.
  */
