import acceptedBrandsCheck from './acceptedBrandsCheck';
import binCheck from './binCheck';
import { brands } from './brandBinMatrix';
import getRawValue from './getRawValue';
import checkMod from './modCheck';
import getPossibleBrand from './possibleBrand';
import { AMEX, LENGTH_AMEX, LENGTH_DEFAULT } from '../constants';

export default (maskedCardNumber, conditions = {}) => {
  const {
    cardBins = null,
    checkMods = false,
    partnerAcceptedBrands = brands,
    shouldCheckAcceptedBrands = false,
    validateMod11 = false
  } = conditions;
  const rawValue = getRawValue(maskedCardNumber);
  const possibleBrand = getPossibleBrand(rawValue, cardBins);
  const maxLength = possibleBrand === AMEX ? LENGTH_AMEX : LENGTH_DEFAULT;
  const passesMod = checkMods
    ? maskedCardNumber.length === maxLength && checkMod(maskedCardNumber, validateMod11)
    : true;
  const isSupportedBrand = shouldCheckAcceptedBrands
    ? acceptedBrandsCheck(possibleBrand, partnerAcceptedBrands)
    : true;
  const isValidBin = binCheck(rawValue, cardBins);
  const isValid = shouldCheckAcceptedBrands ? isSupportedBrand && isValidBin : isValidBin;

  return {
    brand: possibleBrand,
    isSupported: isSupportedBrand,
    isValid,
    passesMod,
    value: maskedCardNumber
  };
};
