import { ELECTRON, VISA } from '../constants';

/**
 * @param {array} brands
 * @returns {array}
 */
export default brands => {
  const brandsLen = brands.length;
  const unfilteredArr = [];

  for (let brand = 0; brand < brandsLen; brand++) {
    if (brands[brand] === ELECTRON) {
      unfilteredArr.push(VISA);
    } else {
      unfilteredArr.push(brands[brand]);
    }
  }
  // remove possible duplicates and return the result
  return unfilteredArr.filter((v, i, a) => a.indexOf(v) === i);
};
