// Component rendering-related items
export const COMPONENT_CLASSNAME = 'vds-input-card';
export const COMPONENT_WRAPPER_CLASSNAME = 'vds-cardnumber';
export const INDICATOR_CLASSNAME = 'vds-card-indicator';
export const INDICATOR_WRAPPER_CLASSNAME = 'vds-input-option';
export const LENGTH_AMEX = 17; // 15-digit card number plus 2 masked spaces
export const LENGTH_DEFAULT = 19; // 16-digit card number plus 3 masked spaces
export const FIVE_DIGITS = [/\d/, /\d/, /\d/, /\d/, /\d/];
export const FOUR_DIGITS = [/\d/, /\d/, /\d/, /\d/];
export const SIX_DIGITS = [/\d/, /\d/, /\d/, /\d/, /\d/, /\d/];
export const AMEX_MASK = [...FOUR_DIGITS, ' ', ...SIX_DIGITS, ' ', ...FIVE_DIGITS];
export const NON_AMEX_MASK = [
  ...FOUR_DIGITS,
  ' ',
  ...FOUR_DIGITS,
  ' ',
  ...FOUR_DIGITS,
  ' ',
  ...FOUR_DIGITS
];

/**
 * Card Brands
 *
 * All brands are used for when there is at least one digit entered by the user
 *
 */
export const AMEX = 'AMEX';
export const DISCOVER = 'DISCOVER';
export const ELECTRON = 'ELECTRON';
export const ELO = 'ELO';
export const INVALID = 'INVALID';
export const MASTERCARD = 'MASTERCARD';
export const UNKNOWN = 'UNKNOWN';
export const VISA = 'VISA';

// Card Validation Errors
export const EMPTY = 'EMPTY';
export const INVALID_BIN = 'INVALID_BIN';
export const INVALID_BIN_BRAND = 'INVALID_BIN_BRAND';
export const INVALID_GENERIC = 'INVALID_GENERIC';
export const NOT_SUPPORTED = 'NOT_SUPPORTED';
export const PARTIAL = 'PARTIAL';

// These errors are all triggered onChange except where noted
export const ERROR_MAP = {
  empty: EMPTY, // field is empty
  invalid_bin: INVALID_BIN, // first digit does not map to a known card brand
  invalid_bin_brand: INVALID_BIN_BRAND, // brand is recognized but bin range is not valid
  invalid_generic: INVALID_GENERIC, // card number is invalid for some other reason not specified
  not_supported: NOT_SUPPORTED, // brand is recognized but not supported by merchant / partner
  partial: PARTIAL // partial value, triggered only onBlur
};
