import { brands } from './brandBinMatrix';
import { UNKNOWN } from '../constants';

/**
 * @description check user-entered cardBrand against merchant- and partner-supported brands.
 * Call function only if shouldValidateAcceptedCards prop is true in your component.
 *
 * @param {string} cardBrand
 * @param {array} acceptedBrands defaults to all brands
 * @return {bool}
 */
export default (cardBrand, acceptedBrands = brands) => {
  const acceptedBrandsLength = acceptedBrands.length;
  let isAccepted = false;
  if (cardBrand === UNKNOWN) {
    return true;
  }

  for (let brand = 0; brand < acceptedBrandsLength; brand++) {
    if (acceptedBrands[brand] === cardBrand) {
      isAccepted = true;
      break;
    }
  }

  return isAccepted;
};
