import { AMEX, ERROR_MAP, INVALID, LENGTH_AMEX, LENGTH_DEFAULT } from '../constants';
import checkMod from './modCheck';

export const validator = (isBlur, conditions = {}) => {
  const {
    brand,
    checkMods = false,
    checkMod11 = false,
    isSupported = true,
    isValid = true,
    value
  } = conditions;
  const maxLength = brand === AMEX ? LENGTH_AMEX : LENGTH_DEFAULT;
  const isPartialValue = value.length < maxLength;
  let error;

  /**
   * `!value` means the field is empty (`ERROR_MAP.empty`)
   * `!isSupported && brand !== INVALID` means entered value maps to a known card brand
   but that brand is not supported by the partner or merchant. (`ERROR_MAP.not_supported`)
   * `brand === INVALID` means the entered value does not map to a known card brand. (`ERROR_MAP.invalid_bin`)
   * `!isValid` means the value maps to a known card brand but the value's bin is not valid for that card brand.
   (`ERROR_MAP.invalid_bin_brand`)
   * `!isPartialValue && checkMods && !checkMod(value, checkMod11)` is for when there is a full and complete
   value for the card brand but the value does not pass mod checks, when mod checks are enabled. (`ERROR_MAP.invalid_generic`)
   * `isPartialValue && isBlur` means that the entered value is a partial value for that card brand. (`ERROR_MAP.partial`)
   */
  if (!value) {
    error = ERROR_MAP.empty;
  } else if (!isSupported && brand !== INVALID) {
    error = ERROR_MAP.not_supported;
  } else if (brand === INVALID) {
    error = ERROR_MAP.invalid_bin;
  } else if (!isValid) {
    error = ERROR_MAP.invalid_bin_brand;
  } else if (!isPartialValue && checkMods && !checkMod(value, checkMod11)) {
    error = ERROR_MAP.invalid_generic;
  } else if (isPartialValue && isBlur) {
    error = ERROR_MAP.partial;
  }

  return error;
};
