import {
  adjustCaretPosition,
  conformToMask,
  cleanMask,
  safeSetSelection,
  isHTMLInputElement,
  isNil,
  NO_INPUT_ERROR
} from './index';

/**
 * This function takes a configuration and returns an object with an update method.
 * The update method is used to conform the raw value to the mask you provide in the config
 *
 * @param {Object} config
 * @return {{state: Object, update: Function}}
 */
export default function createTextMaskInputElement(config = {}) {
  const state = {
    previousConformedValue: undefined,
    previousPlaceholder: undefined
  };

  return {
    state,

    update(
      rawValue,
      { guide = false, inputElement, mask, placeholderChar = 'X', upperCaseValue = false } = config
    ) {
      if (!isHTMLInputElement(inputElement)) {
        throw new Error(NO_INPUT_ERROR);
      }

      const { selectionEnd: currentCaretPosition, value: inputValue } = inputElement;

      if (isNil(rawValue)) {
        rawValue = inputValue;
      } else {
        rawValue = rawValue.toString();
      }

      const { mask: cleanedMask, placeholder, nonRegexChars, placeholderChars } = cleanMask(
        mask,
        placeholderChar
      );

      if (rawValue === state.previousConformedValue && placeholder === state.previousPlaceholder) {
        return {
          ...state,
          adjustedCaretPosition: currentCaretPosition,
          conformedValue: rawValue
        };
      }

      const { conformedValue } = conformToMask(rawValue, cleanedMask, {
        currentCaretPosition,
        guide,
        placeholder,
        placeholderChars,
        previousConformedValue: state.previousConformedValue
      });

      const adjustedCaretPosition = adjustCaretPosition({
        conformedValue,
        currentCaretPosition,
        nonRegexChars,
        placeholder,
        placeholderChars,
        previousConformedValue: state.previousConformedValue,
        previousPlaceholder: state.previousPlaceholder,
        rawValue
      });

      // Text Mask sets the input value to an empty string when the condition below is set. It provides a better UX.
      const inputValueShouldBeEmpty = conformedValue === placeholder && adjustedCaretPosition === 0;
      const inputElementValue = inputValueShouldBeEmpty
        ? ''
        : upperCaseValue
        ? conformedValue.toUpperCase()
        : conformedValue;

      state.previousConformedValue = inputElementValue;
      state.previousPlaceholder = placeholder;

      inputElement.value = inputElementValue;
      safeSetSelection(inputElement, adjustedCaretPosition);

      return {
        ...state,
        adjustedCaretPosition,
        conformedValue: inputElementValue
      };
    }
  };
}
