/* eslint-disable spellcheck/spell-checker */
import { brandMatrix } from './brandBinMatrix';
import { ELO, INVALID, UNKNOWN } from '../constants';

/**
 * @description Checks if the entered card number is for an Elo bin
 *
 * @param {string} cardNum
 * @param {string} cardBrand
 * @param {array} eloBins
 * @returns {string}
 */
export const eloOrOther = (cardNum, cardBrand, eloBins) => {
  if (eloBins && eloBins.length > 0) {
    for (let i = 0; i < eloBins.length; i++) {
      if (cardNum.indexOf(eloBins[i].toString()) === 0) {
        // cardNum starts with an Elo prefix and is definitely Elo
        return ELO;
      } else if (eloBins[i].toString().indexOf(cardNum) === 0) {
        // cardNum is part of an Elo prefix but not confirmed Elo
        return UNKNOWN;
      }
    }
  }
  return cardBrand;
};

/**
 * @description Checks what is the cardBrand based on provided value
 *
 * @param {string} cardNum
 * @param {array} eloBins
 * @returns {string}
 */
const getPossibleBrand = (cardNum, eloBins) => {
  let possibleBrand = UNKNOWN;
  if (!cardNum.length) {
    return possibleBrand;
  }
  const firstDigit = cardNum[0];
  possibleBrand = brandMatrix[firstDigit] || INVALID;
  possibleBrand = possibleBrand === INVALID ? INVALID : eloOrOther(cardNum, possibleBrand, eloBins);

  return possibleBrand;
};

export default getPossibleBrand;
