import { NO_MASK_ERROR } from './index';

const isAndroid = typeof navigator !== 'undefined' && /Android/i.test(navigator.userAgent);
const defer = typeof requestAnimationFrame === 'undefined' ? setTimeout : requestAnimationFrame;

/**
 * Set cursor position on an input element
 * @param {HTMLElement} element
 * @param {Number} selectionPosition
 */
export function safeSetSelection(element, selectionPosition) {
  if (document.activeElement === element) {
    if (isAndroid) {
      defer(() => element.setSelectionRange(selectionPosition, selectionPosition, 'none'), 0);
    } else {
      element.setSelectionRange(selectionPosition, selectionPosition, 'none');
    }
  }
}

/**
 * Tells you whether a value is a string
 * @param {String} value
 * @return {Boolean}
 */
export function isString(value) {
  return typeof value === 'string' || value instanceof String;
}

/**
 * Tells you whether a value is a array
 * @param {String} value
 * @return {Boolean}
 */
export function isArray(value) {
  return (Array.isArray && Array.isArray(value)) || value instanceof Array;
}

/**
 * Tells you whether a value is nil
 * @param {String} value
 * @return {Boolean}
 */
export function isNil(value) {
  return typeof value === 'undefined' || value === null;
}

/**
 * Tells you whether an element is an input type
 * @param {HTMLElement} element
 * @return {Boolean}
 */
export function isHTMLInputElement(element) {
  return (
    (element instanceof Element || element instanceof HTMLDocument) &&
    element.nodeType === 1 &&
    element.tagName.toLowerCase() === 'input'
  );
}

/**
 * Tells you whether a value is a string and has a length > 0
 * @param {String} value
 * @return {Boolean}
 */
export function validString(value) {
  return isString(value) && value.length > 0;
}

/**
 * Returns the display name of a component
 * @param {Component} WrappedComponent
 * @return {string | * | string}
 */
export function getComponentDisplayName(WrappedComponent) {
  return WrappedComponent.displayName || WrappedComponent.name || 'Component';
}

/**
 * Returns an object containing all the info needed for Text-Mask library
 * @param {Array} mask
 * @param {String} placeholderChar
 * @return {{mask: [], nonRegexChars: [], placeholder: string, placeholderChars: []}}
 */
export function cleanMask(mask, placeholderChar = 'X') {
  if (!isArray(mask)) {
    throw new Error(NO_MASK_ERROR);
  }

  const cleanedMask = mask.reduce(
    (maskObj, char) => {
      const { mask, placeholderChars, placeholder } = maskObj;
      const maskChar = char && char.mask instanceof RegExp ? char.mask : char;
      const placeChar =
        char && typeof char.placeholder === 'string' ? char.placeholder.charAt(0) : placeholderChar;

      mask.push(typeof maskChar === 'string' ? maskChar.charAt(0) : maskChar);

      placeholder.push(
        char instanceof RegExp || char.mask instanceof RegExp
          ? placeChar
          : char.toString().charAt(0)
      );

      if (!placeholderChars.includes(placeChar)) {
        placeholderChars.push(placeChar);
      }

      return maskObj;
    },
    {
      mask: [],
      placeholder: [],
      placeholderChars: []
    }
  );

  // Filter out empty string
  const cleanMask = cleanedMask.mask.filter(Boolean);
  // Create placeholder string representation
  const placeholderStr = cleanedMask.placeholder.join('');
  // Remove any duplicates from placeholder array and filter out empty string
  const nonRegexChars = [...new Set(cleanedMask.placeholder.filter(Boolean))];
  // Only use placeholder chars present in nonRegexChars array
  let placeholderChars = cleanedMask.placeholderChars.filter(char => nonRegexChars.includes(char));
  // If our `placeholderChars` array is empty, default to using `placeholderChar`
  if (placeholderChars.length === 0) {
    placeholderChars = [placeholderChar];
  }

  return {
    mask: cleanMask,
    nonRegexChars,
    placeholder: placeholderStr,
    placeholderChars
  };
}
